@import "../../scss/_mixin.scss";

$line-height: 1.2rem;
$reverseDeg: 180deg;

.container {
  position: relative;
  display: flex;

  color: var(--color-accent-secondary);
  @include desktop {
    &:hover .cardLabel > {
      .itemFront {
        transform: rotateY(-#{$reverseDeg});
      }
      .itemBack {
        transform: rotateY(0deg);
      }
    }
  }
}

.cardCheckbox {
  display: none;
}

.cardLabel {
  width: 30rem;
  height: 40rem;
  perspective: 150rem;
  cursor: pointer;
}

.cardCheckbox:checked + .cardLabel {
  z-index: 1;
  > .itemFront {
    transform: rotateY(-#{$reverseDeg}) scale(1.3);
  }
  > .itemBack {
    transform: rotateY(0deg) scale(1.3);
  }
}

.itemFront {
  transform: rotateY(0deg);
}
.itemBack {
  transform: rotateY($reverseDeg);
}

.item {
  width: 100%;
  height: 100%;
  background-color: var(--color-low-light);
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;
  transition: all 0.5s;
  border-radius: 8px;
  overflow: hidden;

  box-shadow: var(--card-shadow);

  &.itemBack {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    .row {
      display: flex;
      gap: 0.8rem;
      margin-bottom: 2.5rem;
      padding-left: 2rem;
      padding-right: 2rem;
    }
    .name {
      font-size: 2.2rem;
      align-self: center;
      margin-bottom: 1.8rem;
    }
    .title {
      font-weight: bold;
      line-height: $line-height;
      color: var(--color-accent-primary);
    }
    .skillsWrapper {
      display: flex;
      gap: 0.6rem;
      flex-wrap: wrap;
      line-height: $line-height;
    }
  }
}

.img {
  width: 100%;
  height: 35rem;
  object-fit: cover;
  -webkit-user-drag: none;
}

.descWrapper {
  display: flex;
  height: 5rem;

  justify-content: center;
  align-items: center;
  &.fullHeight {
    height: 100%;
  }
  .projectDesc {
    color: var(--color-accent-secondary);
    font-weight: bold;
    font-size: 1.8rem;
  }
}
