@import "./_mixin.scss";

* {
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  --color-primary: #000000;
  --color-secondary: #1d2228;
  --color-accent-primary: #14c18b;
  --color-accent-secondary: #fb8122;
  --color-hight-light: #4a2eeb;
  --color-low-light: #3d4248;
  --color-font-primary: #eee;
  --color-font-secondary: #e0e0e0;
  --color-secondary-shadow: 213deg 25% 2%;
  --theme-btn-background: #4e5359;
  --theme-btn-button: #fabf62;
  --theme-btn-shadow: inset rgb(50, 50, 93) 0px 3px 5px -1px,
    inset rgb(0, 0, 0) 1px 2px 3px -1px;
  --card-shadow: 0.5px 0.3px 0.8px hsl(var(--color-secondary-shadow) / 0.19),
    4.3px 2.1px 6.2px -0.2px hsl(var(--color-secondary-shadow) / 0.23),
    7.9px 4px 11.4px -0.5px hsl(var(--color-secondary-shadow) / 0.28),
    12.8px 6.4px 18.5px -0.7px hsl(var(--color-secondary-shadow) / 0.32),
    20.2px 10.1px 29.1px -0.9px hsl(var(--color-secondary-shadow) / 0.36),
    31.4px 15.7px 45.3px -1.2px hsl(var(--color-secondary-shadow) / 0.4),
    47.7px 23.8px 68.8px -1.4px hsl(var(--color-secondary-shadow) / 0.45);
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%; // 1rem = 10px;

  @include queryWidth(s) {
    font-size: 50%; // 1rem = 8px;
  }
  @include queryWidth(xs) {
    font-size: 43.75%; // 1rem = 7px;
  }
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;

  color: var(--color-font);
  font-size: 1.6rem;
  background-color: var(--color-primary);
}

::selection {
  background-color: var(--color-hight-light);
  color: var(--color-font-primary);
}

.pointer {
  cursor: pointer;
}

.mid-area {
  position: absolute;
  z-index: -1;
  top: 50vh;

  // z-index: 9999;
  // background-color: blue;
  // width: 100%;
  // height: 1px;
}

.end-area {
  position: absolute;
  z-index: -1;
  bottom: 2rem;
}
