@import "../../scss/_mixin.scss";

.container {
  width: 100%;
  height: 500vh;
  position: relative;
}

.animationContainer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  &.absolute {
    position: absolute;
  }
}

.flexContainer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.light {
  position: absolute;
  background-color: var(--color-accent-primary);
  border-radius: 50%;
  animation: lightColor 3.5s linear infinite, lightSize 5s linear infinite;

  &.hide {
    visibility: hidden;
    animation: unset;
  }
}

.cubeContainer {
  position: absolute;
  top: -50vh;
  left: -50vw;
}

.row {
  display: inline-flex;
  margin-top: -3.2rem;
  margin-left: -5rem;

  &:nth-child(even) {
    margin-left: 0.2rem;
  }
}

.hexagon {
  width: 10rem;
  height: 12rem;
  background-color: var(--color-primary);
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
  margin: 0.2rem;
}

.computer {
  position: absolute;
  width: 16rem;
  max-height: 16rem;
}
.screen {
  // background-color: red;
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  top: -6.5rem;
  left: -7.3rem;
  width: 14.7rem;
  height: 8.9rem;
  padding: 0;
  .code {
    @for $i from 1 through 15 {
      &:nth-child(#{$i}) {
        @include scrollAnimation(codeIn#{$i});
      }
    }
  }
}

.code {
  position: absolute;
  margin-right: 0.5rem;
  padding-bottom: 0.37rem;
  font-family: Monospace;
  color: var(--color-accent-primary);
  font-family: "Source Code Pro", monospace;
}

.intro {
  position: fixed;
  font-size: 2.5rem;
  top: calc($headerHeight + 3rem);
  left: 10rem;
  color: var(--color-font-primary);
  @include queryWidth(s) {
    left: 5rem;
  }
  @include scrollAnimation(introOut);

  .accent {
    color: var(--color-accent-primary);
    @include scrollAnimation(introOut);
  }
}

.introSec {
  position: absolute;
  font-size: 2.5rem;
  top: calc($headerHeight + 12rem);
  left: 10rem;
  @include queryWidth(s) {
    left: 5rem;
  }
  @include scrollAnimation(introSecIn);

  .accent {
    @include scrollAnimation(introAccentSecIn);
  }
}

@keyframes lightColor {
  0% {
    filter: hue-rotate(0deg) blur(50px);
  }
  100% {
    filter: hue-rotate(360deg) blur(50px);
  }
}

@keyframes lightSize {
  0%,
  100% {
    width: 10rem;
    height: 10rem;
  }
  50% {
    width: 28rem;
    height: 28rem;
  }
}
@keyframes lightSize2 {
  0%,
  100% {
    width: 18rem;
    height: 18rem;
  }
  50% {
    width: 25rem;
    height: 25rem;
  }
}

@keyframes introOut {
  10%,
  100% {
    color: transparent;
  }
}

@keyframes introSecIn {
  0%,
  69% {
    color: transparent;
  }
  80%,
  100% {
    color: var(--color-font-primary);
  }
}

@keyframes introAccentSecIn {
  0%,
  69% {
    color: transparent;
  }
  80%,
  100% {
    color: var(--color-accent-primary);
  }
}

