$width: 6.8rem;
$height: 3.5rem;
$buttonHeight: 2.4rem;

.container {
  display: flex;
  align-items: center;
  position: relative;
  width: $width;
  height: $height;
  border-radius: $height;
  background-color: var(--theme-btn-background);
  box-shadow: var(--theme-btn-shadow);
}

$left: 0.8rem;
.button {
  position: relative;
  left: $left;
  width: $buttonHeight;
  height: $buttonHeight;
  border-radius: 50%;
  background-color: var(--theme-btn-button);
  transition: all 0.4s;
  &.dark {
    left: calc($width - $left * 4);
    &::before {
      content: "";
      position: absolute;
      height: 70%;
      width: 70%;
      background-color: var(--theme-btn-background);
      border-radius: 50%;
      left: -20%;
      top: 8%;
    }
  }
}
