@import "../../scss/_mixin.scss";

.container {
  height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--color-primary);
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -#{$headerHeight};
  color: var(--color-font-primary);
  &.active {
    .title {
      transition: transform 0.8s, background-position 4s;
      background-position: 0%;
      transform: unset;
      @include queryWidth(s) {
        transition: transform 0.8s, background-position 3s;
      }
    }

    .desc {
      transition: all 0.8s ease 0.3s;
      transform: unset;
      color: var(--color-font-primary);
    }

    .mailWrapper {
      transform: unset;
      transition: all 0.5s ease 0.7s;
    }
  }
}

.title {
  margin-top: 5rem;
  font-size: 5rem;
  font-weight: bold;

  @include imageText(
    linear-gradient(
      120deg,
      var(--color-accent-primary) 50%,
      var(--color-low-light) 50%
    )
  );

  background-size: 400%;
  background-position: 100%;

  transition: transform 0.4s, background-position 0.4s;
  transform: translateX(100vw);
  transform-origin: center;
}

.desc {
  transform: translateX(-100vw);
  font-size: 2rem;
  color: var(--color-accent-primary);
}

.mailWrapper {
  position: relative;
  margin-top: 8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32rem;
  padding: 2rem;
  border-radius: 4px;
  background-color: var(--color-low-light);
  transform: translateY(100vh);
  transition: all 0.5s;

  @include queryWidth(s) {
    width: 40rem;
  }

  .mail {
    color: var(--color-font-secondary);
    cursor: pointer;

    @include queryWidth(s) {
      font-size: 2rem;
    }
  }

  .icon {
    position: absolute;
    right: 1rem;
  }

  .svg {
    width: 100%;
    max-height: 1.5rem;
    height: 100%;
    visibility: hidden;

    @include mobile {
      visibility: visible;
      max-height: 2.8rem;
    }

    path:nth-child(1) {
      fill: var(--color-accent-primary);
    }
    path:nth-child(2) {
      fill: var(--color-accent-secondary);
    }
  }

  &:hover {
    .svg {
      visibility: visible;
      max-height: 2.8rem;
      transition: all 0.3s;
    }
  }
}

.activeArea {
  height: 50%;
  position: absolute;
  z-index: -1;
  bottom: 0;
}
