@import "../../scss/_variables.scss";
@import "../../scss/_mixin.scss";
.container {
  position: fixed;
  height: $headerHeight;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  z-index: 10;
  background-color: var(--color-primary);
  border-bottom: 1px solid #333;
  transition: all 0.2s;

  &.light {
    background-color: var(--color-secondary);
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.6);
    border-bottom: 1px solid var(--color-secondary);
    transition: all 0.5s;

    .links {
      @include a {
        color: var(--color-font-secondary);
        &.active {
          color: var(--color-accent-secondary);
        }
      }
    }
  }
}

.menuCheckbox {
  display: none;

  @include queryWidth(s) {
    &:checked {
      & + .menuButton > .hamburger {
        background: transparent;
        &::before {
          top: 0;
          transform: rotate(-45deg);
        }
        &::after {
          top: 0;
          transform: rotate(45deg);
        }
      }

      & ~ .links {
        visibility: visible;
        transform: translateX(0);
        background-color: var(--color-low-light);
        box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
          rgba(0, 0, 0, 0.22) 0px 15px 12px;
      }
    }
  }
}

.menuButton {
  display: none;
  height: 4rem;
  width: 4rem;
  margin-right: 2rem;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  color: var(--color-font-primary);
  @include queryWidth(s) {
    display: flex;
  }
}

.hamburger {
  position: relative;
  &,
  &::before,
  &::after {
    width: 3rem;
    height: 0.25rem;
    border-radius: 0.1rem;
    background-color: currentColor;
    transition: all 0.2s;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    transform-origin: center center;
  }

  &::before {
    top: -1rem;
  }

  &::after {
    top: 1rem;
  }
}

.links {
  display: flex;
  align-items: center;
  gap: 2rem;
  padding-right: 3rem;
  @include queryWidth(s) {
    display: flex;
    z-index: 9;
    position: absolute;
    background-color: transparent;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    top: calc($headerHeight + 1rem);
    right: 1rem;

    visibility: hidden;
    transform: translateX(15rem);
    transition: all 0.3s;
    @include a {
      font-size: 2.5rem;
    }
  }
}

@include a {
  font-size: 2rem;
  text-decoration: none;
  color: var(--color-font-primary);
  &.active {
    color: var(--color-accent-primary);
  }
}
